import Link from 'next/link'

export default props => {
  const { className, to, href, target, rel, onClick, children, ...rest } = props
  const ariaLabel = props['aria-label']

  return (
    <Link href={to || href} {...rest} passHref>
      <a
        className={className}
        target={target}
        rel={rel}
        onClick={onClick}
        aria-label={ariaLabel}>
        {children}
      </a>
    </Link>
  )
}
